.social-links-form {
  max-width: 500px;
  margin: auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.1);
}

label {
  display: block;
  margin-bottom: 10px;
}

input {
  width: 100%;
  padding: 8px;
  margin-bottom: 15px;
}

button {
  margin-top: 10px;
  padding: 10px 15px;
  margin: 0 4px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

/* General styling */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f0f2f5;
  margin: 0;
  padding: 0;
}

/* Profile container */
.profile-container {
  background-color: #ffffff;
  padding: 20px;
  max-width: 600px;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

/* Profile image */
.profile-image {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #4caf50;
  margin-bottom: 20px;
}

/* Profile heading */
h2 {
  font-size: 28px;
  color: #333333;
  margin: 10px 0;
}

h3 {
  font-size: 20px;
  color: #555555;
  margin-bottom: 10px;
}

/* Short title */
.short-title {
  font-size: 18px;
  color: #888888;
  margin-bottom: 20px;
}

/* Social links container */
.social-links {
  list-style: none;
  padding: 0;
  margin: 20px 0;
}

.social-links li {
  margin-bottom: 10px;
}

/* Social link styling */
.social-links a {
  text-decoration: none;
  color: #ffffff;
  background-color: #4caf50;
  padding: 10px 20px;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.social-links a:hover {
  background-color: #45a049;
}

/* Vanity link */
.vanity-link {
  font-size: 16px;
  color: #666666;
  margin-top: 20px;
  display: block;
  font-style: italic;
}

/* Loading and error styles */
.loading, .error {
  font-size: 18px;
  color: #ff0000;
  margin: 20px 0;
}
button:hover{
background-color: white;
}







